import React, { useEffect, useState } from 'react';
import { Container, Image } from 'react-bootstrap';
import "bootstrap/dist/css/bootstrap.min.css"

import {ApiService} from '../../service/ApiService'
import Dimp from '../../Images/DIMP.png'
import "./dimpStyle.css"

export default function SimpleContainer() {

    const [state, setState] = useState({
        cnpjcpf:'',
        im:'',
        processoConc:'',
        UpDeferimento:'',
        periodoReferencia:'',
    })
    useEffect(()=>{
        ApiService.get('/dimp').then(res=>{
            setState(res.data)
        })
    }, [])

    return (
        <Container>
            <figure className="position-relative">
                <img src={Dimp} alt="img" className="img-fluid" />
                <figcaption id="cnpjcpf">asd</figcaption>
                <figcaption id="im">im</figcaption>
                <figcaption id="processoConc">proces</figcaption>
                <figcaption id="UPDefererimento">deferer</figcaption>
                <figcaption id="periodoReferencia">refere</figcaption>


                <figcaption id="concedido">conced</figcaption>
                <figcaption id="estabelecido">Estabelecido</figcaption>
                <figcaption id="seguintesOpera">opera</figcaption>
                <figcaption id="Obs">obs</figcaption>
                <figcaption id="restricoes">res</figcaption>
            </figure>
            

        </Container>
    );
}
