import React, { useContext } from 'react';
import clsx from 'clsx';
import { makeStyles } from '@material-ui/core/styles';
import Drawer from '@material-ui/core/Drawer';
import Button from '@material-ui/core/Button';
import List from '@material-ui/core/List';
import Divider from '@material-ui/core/Divider';
import ListItem from '@material-ui/core/ListItem';

import MenuIcon from '@material-ui/icons/Menu';
import { Link } from 'react-router-dom';
import StoreContext from '../../components/Store/Context';

import ListAltIcon from '@material-ui/icons/ListAlt';
import ExitToAppIcon from '@material-ui/icons/ExitToApp';
import AllInboxIcon from '@material-ui/icons/AllInbox';
import DashboardIcon from '@material-ui/icons/Dashboard';
import {ListItemIcon, ListItemText} from '@material-ui/core'
import logo from '../../Images/LogoSidebar.png'
const rotas = [
  {
    routeName: 'Moderao',
    path: '/moderador',
    icone:<AllInboxIcon />
  },
  {
    routeName: 'Dashboard',
    path: '/dashboard',
    icone:<AllInboxIcon />
  }
 
]

const useStyles = makeStyles({
  list: {
    width: 240,
  },
  fullList: {
    width: 'auto',
  },
  div_links:{
    marginTop:'25px'
  },
  links: {
    marginTop: '15px',
    marginBottom:'20px',
    padding:'10px'
  },
  paper: {
    position:'relative',
    height: 'calc(100% - 64px)',
    top: 55,
  },
  img:{
    marginLeft:'32px',
    marginBottom:'18px',
    padding:0,
    width:'120px'
  }
  
});



export default function SideBar() {
  const classes = useStyles();
  const [state, setState] = React.useState({
    top: false
  });

  const { token ,setToken } = useContext(StoreContext);

  function logout(){
    console.log(token)
    localStorage.removeItem('token');
    sessionStorage.removeItem('token');
    sessionStorage.clear();
    localStorage.clear();
    setToken(null);
  }

  const toggleDrawer = (anchor, open) => (event) => {
    if (event.type === 'keydown' && (event.key === 'Tab' || event.key === 'Shift')) {
      return;
    }

    setState({ ...state, [anchor]: open });
  };

  const list = (anchor) => (
    <div
    className={classes.paper} 
      role="presentation"
      onClick={toggleDrawer(anchor, false)}
      onKeyDown={toggleDrawer(anchor, false)}
    >
      {/* <img src={logo}  className={classes.img} alt="logo" /> */}
      <List>
        {rotas.map((rota, index) => (
          <ListItem className={classes.links} button key={index}>
            <ListItemIcon>{rota.icone}</ListItemIcon>
            <Link to={rota.path} ><ListItemText primary={rota.routeName} /></Link>
          </ListItem>
        ))}
      </List>
      
      <Divider />
      <ListItem> 
        <ListItemIcon> <ExitToAppIcon /></ListItemIcon>
        {/* <Link to="/"><ListItemText>Logout</ListItemText></Link> */}
        <button type="button" onClick={() => logout()}>
              Sair
        </button>
      </ListItem>
    </div>
  );

 
  return (
    <div >
      {['left'].map((anchor) => (
        <React.Fragment key={anchor}>
          <Button onClick={toggleDrawer(anchor, true)}>
            <MenuIcon />
          </Button>
          <Drawer style={{ flexShrink: 0}} anchor={anchor} open={state[anchor]} onClose={toggleDrawer(anchor, false)}>
            {list(anchor)}
          </Drawer>
        </React.Fragment>
      ))}
    </div>
  );
}


