import React, { useState, useEffect, useContext } from 'react';
import { useHistory } from 'react-router-dom';
import StoreContext from '../Store/Context';
import Button from '@material-ui/core/Button';
import CssBaseline from '@material-ui/core/CssBaseline';
import TextField from '@material-ui/core/TextField';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Checkbox from '@material-ui/core/Checkbox';
import Pdf from '../Pdf/privacidade.pdf';
import Grid from '@material-ui/core/Grid';
import { makeStyles } from '@material-ui/core/styles';
import Container from '@material-ui/core/Container';
import { withRouter, Redirect } from 'react-router-dom';
import { Link } from 'react-router-dom';
import { ApiService } from '../../service/ApiService';
import { Alert } from '@material-ui/lab';



const preventDefault = (event) => event.preventDefault();

const useStyles = makeStyles((theme) => ({
    paper: {
        marginTop: theme.spacing(8),
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
        backgroundColor: 'white',
    },
    avatar: {
        margin: theme.spacing(1),
        backgroundColor: 'white',
    },
    form: {
        width: '100%', // Fix IE 11 issue.
        marginTop: theme.spacing(1),
    },
    submit: {
        margin: theme.spacing(3, 0, 2),
    },
    alert: {
        marginTop: '50px'
    }
}));

function initialState() {
    return { user: '', password: '' };
  }
  

function SignIn(props) {

  const [values, setValues] = useState(initialState);
  const [error, setError] = useState(null);
  const { setToken } = useContext(StoreContext);
  const { token } = useContext(StoreContext);
  const history = useHistory();


    const classes = useStyles();
    const [redirect, setRedirect] = useState(false)
    const [state, setState] = React.useState({
        username: '',
        password: '',
        access: '',
    })

    const [validLogin, setValidLogin] = useState('')
    const handleChange = (e) => {
        const { id, value } = e.target
        setState(prevState => ({
            ...prevState,
            [id]: value
        }))
        console.log(id + ' ' + value)
    }

    function handleSubmit(e) {
        e.preventDefault()
        ApiService.post('/sessions', state)
            .then(res => {
                console.log(res.status)
                if (res.status === 200) {

                    // localStorage.removeItem('token');
                    // sessionStorage.removeItem('token');
                    // setToken(null);
                    sessionStorage.setItem('token', res.data.token)
                    sessionStorage.setItem('email', res.data.email)
                    sessionStorage.setItem('access', res.data.access)
                    localStorage.setItem(token, res.data.token);

                      setToken(res.data.token);
                      if(res.data.access == "MODERADOR"){
                        return history.push('/moderador');
                      }
                      else if(res.data.access == "ESTABELECIMENTO"){
                        return history.push('/perfil');
                      }
                      
                    
                    setError(error);
                    setValues(initialState);
                }

            }).catch(error => {
                setValidLogin(false)
                console.log('ERRO: '+error)
            })
            .catch(function (error) {
                console.log(error);
            });
    }


    function loginHandle() {
        if (validLogin === '') {
            return 'empty'
        }
        if (validLogin) {
            return 'success'
        }
        if (!validLogin) {
            return 'warning'
        }
    }


    const { from } = props.location.state || '/'
 
    return (

        <Container component="main" maxWidth="xs">
            <CssBaseline />
            <div className={classes.paper}>

                <form onSubmit={handleSubmit} className={classes.form} noValidate>

                    <TextField
                        label="CPF ou cnpj"
                        variant="outlined"
                        margin="normal"
                        required
                        fullWidth
                        id="username"
                        placeholder="CPF ou Cnpj"
                        name="cpf_cnpj"
                        autoComplete="cpf_cnpj"
                        autoFocus
                        value={state.username}
                        onChange={handleChange}

                    />
                    <TextField
                        label="Senha"
                        variant="outlined"
                        margin="normal"
                        required
                        fullWidth
                        name="password"
                        placeholder="Senha"
                        type="password"
                        id="password"
                        autoComplete="current-password"
                        value={state.password}
                        onChange={handleChange}
                    />

                    <FormControlLabel
                        control={<Checkbox value="remember" color="primary" />}
                        label="Lembrar Senha"
                    />
                    <Button
                        type="submit"
                        fullWidth
                        variant="contained"
                        color="primary"
                        className={classes.submit}
                    >
                        Entrar
                    </Button>
                    <Grid container direction='column'>
                        <Grid item xs>
                            <Link variant="body2">
                                Esqueceu a senha?
                            </Link>
                        </Grid>
                        <Grid item>
                            <Link to="/cadastro-estabelecimento" variant="body2">
                                No tem uma conta? Cadastre-se
                            </Link>
                        </Grid>
                        <Grid item>
                            <Link to="/validacao-dimp" variant="body2">
                                Validar Dimp
                            </Link>
                        </Grid>
                        <Grid item>
                           
                            <a href={Pdf} target='_blank'>Termos de privacidade</a>
                           
                        </Grid>
                    </Grid>
                    {}
                    {redirect &&(
                        <Redirect to={from || '/moderador'} />
                    )}
                </form>
            </div>
            <div className={classes.alert}>
                {loginHandle() === 'warning' ? <Alert severity="error">Login invalido</Alert> : ''}
                {loginHandle() === 'empty' ? '' : ''}
                {loginHandle() === 'success' ? <Alert severity="success">Login vlido</Alert> : ''}
            </div>

            {/* <Alert severity="error">Login invalido</Alert> */}

        </Container>
    )
    // }
}
export default withRouter(SignIn)