
// CONSUMIR API PARA LISTAGEM DE ESTADOS E MUNICIPIOS
import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';
import { TextField } from '@material-ui/core';
import CssBaseline from '@material-ui/core/CssBaseline';
import Container from '@material-ui/core/Container';
import InputLabel from '@material-ui/core/InputLabel';
import MenuItem from '@material-ui/core/MenuItem';
import FormControl from '@material-ui/core/FormControl';
import Select from '@material-ui/core/Select';
import { useState } from 'react';
import { Form } from 'react-bootstrap'
import axios from 'axios'
import { constant } from 'async';

const useStyles = makeStyles((theme) => ({
    root: {
        flexGrow: 1,
    },
    formControl: {
        margin: theme.spacing(1),
        minWidth: 120,
    },
    paper: {
        padding: theme.spacing(2),
        textAlign: 'center',
        color: theme.palette.text.secondary,
    },
}));



export default function Adress() {
    const classes = useStyles();

    const estados = [] // CONSUMIR COM API
    const municipios = [] // CONSUMIR COM API

    // console.log(estados)
    const [state, setState] = useState({
        uf: '',
        municipio: 's',
        cep: '',
        bairro: '',
        endereco: '',
        numero: '',
        complemento: '',
        referencia: '',

    })
    function getCep() {
        axios.get(`https://viacep.com.br/ws/${state.cep}/json/`).then(req => {
            // console.log(req.data)
            setState({
                uf: req.data.uf,
                municipio: req.data.localidade,
                bairro: req.data.bairro,
                endereco: req.data.logradouro,
            })
        })
    }
    
    const handleChange = (e) => {
        const { id, value } = e.target
        //condicional cep
        // if(id == "cep"){
        //     getCep()
        // }
        setState(prevState => ({
            ...prevState,
            [id]: value
        }))
        console.log(id + ' ' + value)
    }
    return (
        <div className={classes.root}>

            <React.Fragment>

                <Container maxWidth="md">
                    <h3>Endereo</h3>
                    <form className={classes.root} noValidate autoComplete="off">
                        <Grid
                            container
                            direction="row"
                            justifyContent="flex-start"
                            alignItems="flex-start"
                        >

                            <Form.Group className={classes.formControl}>
                                <Form.Label>CEP</Form.Label>
                                <Form.Control id="cep" onChange={handleChange} value={state.cep} required type="text" />
                            </Form.Group>
                            <Form.Group className={classes.formControl}>
                                <Form.Label>Endereo</Form.Label>
                                <Form.Control id="endereco" onChange={handleChange} value={state.endereco} required type="text" />
                            </Form.Group>
                            <Form.Group className={classes.formControl}>
                                <Form.Label>Numero</Form.Label>
                                <Form.Control id="numero" onChange={handleChange} value={state.numero} required type="text" />
                            </Form.Group>

                        </Grid>
                        <Grid
                            container
                            direction="row"
                            justifyContent="flex-start"
                            alignItems="flex-start"
                        >
                            <Form.Group className={classes.formControl}>
                                <Form.Label>Complemento</Form.Label>
                                <Form.Control id="complemento" onChange={handleChange} value={state.complemento} required type="text" />
                            </Form.Group>
                            <Form.Group className={classes.formControl}>
                                <Form.Label>Bairro</Form.Label>
                                <Form.Control id="bairro" onChange={handleChange}  value={state.bairro} required type="text" />
                            </Form.Group>


                        </Grid>
                        <Grid
                            container
                            direction="row"
                            justifyContent="flex-start"
                            alignItems="flex-start"
                        >

                            <FormControl className={classes.formControl} className={classes.formControl}>
                                <InputLabel id="uf">UF</InputLabel>
                                <Select
                                    labelId="demo-simple-select-label"
                                    id="demo-simple-select"
                                    value={state.uf}
                                    onChange={handleChange}
                                > {estados.map((uf, index) => {
                                    return <MenuItem value={uf.nome}>{uf.nome}</MenuItem>
                                })}
                                </Select>
                            </FormControl>
                            <FormControl className={classes.formControl}>
                                <InputLabel id="municipio">Municpo</InputLabel>
                                <Select
                                    labelId="demo-simple-select-label"
                                    id="demo-simple-select"
                                    value={state.municipio}
                                    onChange={handleChange}
                                >
                                    {municipios.map((municipio) => {
                                        return <MenuItem value={municipio.nome}>{municipio.nome}</MenuItem>
                                    })}
                                </Select>
                            </FormControl>
                        </Grid>
                    </form>
                </Container>
            </React.Fragment>
        </div>
    );
}
