import React from 'react';
import {
  BrowserRouter as Router,
  Switch,
  Route,
} from 'react-router-dom';
import StoreProvider from './components/Store/Provider';
import RoutesPrivate from './components/Routes/Private/Private';
import SentFile from "./components/SentFile";
import Login from "./components/Login";
import TimeLine from "./components/TimeLine";
import Dashboard from "./components/Dashboard";
import Dimps from "./components/Dimps";
import ScreenDimp from './components/ScreenDimp'
import EdicaoProfile  from './components/EditTimeLine'
const PagesRoot = () => (
  <Router>
    <StoreProvider>
      <Switch>
        <Route path="/cadastro-estabelecimento" component={TimeLine} />
        <Route path="/login" component={Login} />
        <Route path="/validacao-dimp" component={ScreenDimp} />
        <RoutesPrivate path="/moderador" component={SentFile} />
        <RoutesPrivate path="/dashboard" component={Dashboard} />
        <RoutesPrivate path="/dimps" component={Dimps} />
        <RoutesPrivate path="/perfil" component={EdicaoProfile} />
      </Switch>
    </StoreProvider>
  </Router>
)


export default PagesRoot;
