import React, { useState, useEffect, forwardRef  } from 'react';
import { Card, Typography, TextareaAutosize, Grid } from '@material-ui/core';
import useStyles from './SentFile';
import { ApiService } from '../../service/ApiService'
import MaterialTable from "material-table";
import VisibilityIcon from '@material-ui/icons/Visibility';

import AddBox from '@material-ui/icons/AddBox';
import ArrowDownward from '@material-ui/icons/ArrowDownward';
import Check from '@material-ui/icons/Check';
import ChevronLeft from '@material-ui/icons/ChevronLeft';
import ChevronRight from '@material-ui/icons/ChevronRight';
import Clear from '@material-ui/icons/Clear';
import DeleteOutline from '@material-ui/icons/DeleteOutline';
import Edit from '@material-ui/icons/Edit';
import FilterList from '@material-ui/icons/FilterList';
import FirstPage from '@material-ui/icons/FirstPage';
import LastPage from '@material-ui/icons/LastPage';
import Remove from '@material-ui/icons/Remove';
import SaveAlt from '@material-ui/icons/SaveAlt';
import Search from '@material-ui/icons/Search';
import ViewColumn from '@material-ui/icons/ViewColumn';
import Button from '@material-ui/core/Button';
import Paper from '@material-ui/core/Paper';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import { Checkbox} from '@material-ui/core';
import { ViewCarouselTwoTone } from '@material-ui/icons';
import NavBar from "../NavBar";


const tableIcons = {
    Add: forwardRef((props, ref) => <AddBox {...props} ref={ref} />),
    Check: forwardRef((props, ref) => <Check {...props} ref={ref} />),
    Clear: forwardRef((props, ref) => <Clear {...props} ref={ref} />),
    Delete: forwardRef((props, ref) => <DeleteOutline {...props} ref={ref} />),
    DetailPanel: forwardRef((props, ref) => <ChevronRight {...props} ref={ref} />),
    Edit: forwardRef((props, ref) => <Edit {...props} ref={ref} />),
    Export: forwardRef((props, ref) => <SaveAlt {...props} ref={ref} />),
    Filter: forwardRef((props, ref) => <FilterList {...props} ref={ref} />),
    FirstPage: forwardRef((props, ref) => <FirstPage {...props} ref={ref} />),
    LastPage: forwardRef((props, ref) => <LastPage {...props} ref={ref} />),
    NextPage: forwardRef((props, ref) => <ChevronRight {...props} ref={ref} />),
    PreviousPage: forwardRef((props, ref) => <ChevronLeft {...props} ref={ref} />),
    ResetSearch: forwardRef((props, ref) => <Clear {...props} ref={ref} />),
    Search: forwardRef((props, ref) => <Search {...props} ref={ref} />),
    SortArrow: forwardRef((props, ref) => <ArrowDownward {...props} ref={ref} />),
    ThirdStateCheck: forwardRef((props, ref) => <Remove {...props} ref={ref} />),
    ViewColumn: forwardRef((props, ref) => <ViewColumn {...props} ref={ref} />)
  };

  
 

function SentFile() {



   //buscando as informaes na api no endpoint que retorna os estabelecimentos pendentes
   const [estabelecimentos, setEstabelecimentos] = useState([])
   useEffect(() => {
       ApiService.get('/moderador/pendentes').then(req => {
        setEstabelecimentos(req.data.data)
          console.log("provedores: " + req.data.data)
          setOpen(false);
       }).catch(error => {
          console.log(error)
       })
    }, [])

    //criao da const que ser enviada no body da requiso para aprocao e reprovao do estabelecimento
    const [estabelecimentoPayload, setEstabelecimentoPayload] = React.useState({
      cnpjcpf: '',
      motivo: ''
    })

    //funo para recarregar a pagina 
    function refreshPage() {
    window.location.reload();
   }

   //funo para aprovar o estabelecimento 
  function handleAprove(e) {
     console.log(e.cnpjcpf)
     setEstabelecimentoPayload({
      cnpjcpf: e.cnpjcpf
     })

      //realiza o post no endpoint reponsavel pela aprovao do estabelecimento
      ApiService.post('/moderador/aprovar', estabelecimentoPayload)
          .then(res => {
              console.log(res.status)
              if (res.status === 200) {
                console.log(res)
                alert("Estabelecimento Aprovado com sucesso!");

                 refreshPage();
              }

          }).catch(error => {
            console.log(error);
          })
          .catch(function (error) {
              console.log(error);
          });
  }

    // funo para reprovao do estabelecimento 
  function handleFail(e) {

    //realiza o post no endpoint reponsavel pela reprovao do estabelecimento
     ApiService.post('/moderador/reprovar', estabelecimentoPayload)
         .then(res => {
             console.log(res.status)
             if (res.status === 200) {
               console.log(res)
               alert("Estabelecimento Reprovado!");
            // refreshPage();
             }else{
              alert(res.response);
             }

         }).catch(error => {
             console.log(error)
             alert("O Estabelecimento no doi reprovado!");
         })

         .catch(function (error) {
             console.log(error);
         });
 }
    
 const handleChange = (e) => {
  const { id, value } = e.target
  setEstabelecimentoPayload(prevState => ({
    ...prevState,
    [id]: value
  }))
  // console.log(state)
}
  
    const [open, setOpen] = React.useState(false);

    const handleClose = () => {
      setOpen(false);
    };

    // Responsavel por abri o modal de reprovao de estabelecimento
    const [openEstablismentModal, setOpenEstablismentModal] = React.useState(false);

    const handleCloseModelFail = () => {
      setOpenEstablismentModal(false);
    };
    
    
    const [estabelecimentoRow, setestabelecimentoRow] = useState({
      nome: "",
      status: "",
      cnpjcpf:"",
      razaoSocial: "",
      ie: "",
      im: "",
      iptu: "",
      respNome: "",
      respEmail: "",
      respCpf: "",
      respTelefone: "",
      respCelular: "",
      respCargo: "",
      contNome: "",
      contEmail: "",
      contCrc: "",
      cep: "",
      endereco: "",
      enderecoNumero: "",
      enderecoComplemento: "",
      bairro: "",
      cidade: "",
      uf: "",
      iduser: "",
      banco: "",
      agencia: "",
      agenciaDigito: "",
      conta:"",
      contaDigito: "",
      
      

    })


    function showModalReproveEstablishment(value){

     console.log(value.cnpjcpf)
   

      setEstabelecimentoPayload({
        cnpjcpf: value.cnpjcpf, 
        motivo: ''
       })

      setOpenEstablismentModal(true);
    }

    // Detalhes do estabelecimento
      function detailsEstablishment(value){
      setestabelecimentoRow({
      nome: value.nome,
      status: value.status,
      cnpjcpf: value.cnpjcpf,
      razaoSocial: value.razaoSocial,
      ie: value.ie,
      im: value.im,
      iptu: value.iptu,
      respNome: value.respNome,
      respEmail: value.respEmail,
      respCpf: value.respCpf,
      respTelefone: value.respTelefone,
      respCelular: value.respCelular,
      respCargo: value.respCargo,
      contNome: value.contNome,
      contEmail: value.contEmail,
      contCrc: value.contCrc,
      cep: value.cep,
      endereco: value.endereco,
      enderecoNumero: value.enderecoNumero,
      enderecoComplemento: value.enderecoComplemento,
      bairro: value.bairro,
      cidade: value.cidade,
      uf: value.uf,
      iduser: value.iduser,
      banco: value.banco,
      agencia: value.agencia,
      agenciaDigito: value.agenciaDigito,
      conta: value.conta,
      contaDigito: value.contaDigito
     

      })

      
      setOpen(true);
    }

    const handleClickOpen = (teste) => {
      setestabelecimentoRow(teste)
      //setOpen(true);
    };

  const classes = useStyles();
  return (
    <div>
      <NavBar />
      <MaterialTable
        icons={tableIcons}
        title="Moderao"
        columns={[
          { title: "Estabelecimento", field: "nome" },
          { title: "CNPJ / CPF", field: "cnpjcpf" },
          { title: "Razo social", field: "razaoSocial" },
          { title: "Responsavel", field: "respNome" },
          { title: "E-mail", field: "respEmail" },
          { title: "Telefone", field: "respTelefone" },
        ]}
        data={estabelecimentos}
        actions={[
          {
            icon: Check,
            tooltip: "Aprovar",
            onClick: (event, rowData) => handleAprove(rowData),
          },
          {
            icon: Clear,
            tooltip: "Reprovar",
            onClick: (event, rowData) => showModalReproveEstablishment(rowData),
          },
          {
            icon: VisibilityIcon,
            tooltip: "Detalhes",

            onClick: (event, rowData) => detailsEstablishment(rowData),
          },
        ]}
        options={{
          actionsColumnIndex: -1,
        }}
      />
      
      
      <Dialog
        open={open}
        onClose={handleClose}
        aria-labelledby="alert-dialog-title"
        aria-describedby="alert-dialog-description"
        className={classes.dialogBox}
      >
        <DialogTitle id="alert-dialog-title">
          Informaes do Estabelecimento
        </DialogTitle>
        <DialogContent>
          <DialogContentText id="alert-dialog-description">
            <div className={classes.root}>
              <Grid container spacing={1}>
                <Grid item xs={12} md={6}>
                  <b>Nome: </b>
                  {estabelecimentoRow.nome}
                </Grid>
                <Grid item xs={12} md={6}>
                  <b>CPF/CNPJ: </b>
                  {estabelecimentoRow.cnpjcpf}
                </Grid>
                <Grid item xs={12} md={12}>
                  <b>Razo Social: </b>
                  {estabelecimentoRow.razaoSocial}
                </Grid>
                <Grid item xs={12} md={6}>
                  <b>Inscr. Estadual: </b> {estabelecimentoRow.ie}
                </Grid>
                <Grid item xs={12} md={6}>
                  <b>Inscr. Municipal: </b>
                  {estabelecimentoRow.im}
                </Grid>

                <Grid item xs={12} md={12}>
                  <b>Responsvel: </b>
                  {estabelecimentoRow.respNome}
                </Grid>

                <Grid item xs={12} md={6}>
                  <b>Email: </b>
                  {estabelecimentoRow.respEmail}
                </Grid>

                <Grid item xs={12} md={6}>
                  <b>Email: </b>
                  {estabelecimentoRow.contEmail}
                </Grid>
                <Grid item xs={12} md={6}>
                  <b>CRC: </b>
                  {estabelecimentoRow.contCrc}
                </Grid>

                <Grid item xs={12} md={6}>
                  <b>CEP: </b>
                  {estabelecimentoRow.cep}
                </Grid>
                <Grid item xs={12} md={6}>
                  <b>Endereo: </b>
                  {estabelecimentoRow.endereco}
                </Grid>
                <Grid item xs={12} md={3}>
                  <b>N: </b>
                  {estabelecimentoRow.enderecoNumero}
                </Grid>
                <Grid item xs={12} md={3}>
                  <b>Complemento: </b>
                  {estabelecimentoRow.enderecoComplemento}
                </Grid>
                <Grid item xs={12} md={6}>
                  <b>Bairro: </b>
                  {estabelecimentoRow.bairro}
                </Grid>
                <Grid item xs={12} md={4}>
                  <b>cidade: </b>
                  {estabelecimentoRow.cidade}
                </Grid>
                <Grid item xs={12} md={2}>
                  <b>UF: </b>
                  {estabelecimentoRow.uf}
                </Grid>
                <Grid item xs={12} md={6}>
                  <b>Telefone: </b>
                  {estabelecimentoRow.respTelefone}
                </Grid>
                <Grid item xs={12} md={6}>
                  <b>Celular: </b>
                  {estabelecimentoRow.respCelular}
                </Grid>
                <Grid item xs={12} md={12}>
                  <DialogTitle id="alert-dialog-title" >
                  Adquirintes
                </DialogTitle>
                </Grid>
                
                <Grid item xs={12} md={6}>
                  <b>Provedor: </b>
                  {estabelecimentoRow.idtefprovedor}
                </Grid>
                <Grid item xs={12} md={6}>
                  <b>Adquirente: </b>
                  {estabelecimentoRow.idadquirente}
                </Grid>
                <Grid item xs={12} md={6}>
                  <b>Outros: </b>
                  {estabelecimentoRow.outros}
                </Grid>
                <Grid item xs={12} md={6}>
                  <b>Tecnologia: </b>
                  {estabelecimentoRow.idtecnologia}
                </Grid>
                <Grid item xs={12} md={6}>
                  <b>Tecnologia (outras): </b>
                  {estabelecimentoRow.outrostecnologia}
                </Grid>
                <Grid item xs={12} md={6}>
                  <b>Pin Pad: </b>
                  {estabelecimentoRow.pinpad}
                </Grid>
                <Grid item xs={12} md={6}>
                  <b>Modelo: </b>
                  {estabelecimentoRow.pinpadModelo}
                </Grid>
                <Grid item xs={12} md={6}>
                  <b>Posto: </b>
                  {estabelecimentoRow.posto}
                </Grid>
                <Grid item xs={12} md={6}>
                  <b>Good Card: </b>
                  {estabelecimentoRow.goodCard}
                </Grid>
                <Grid item xs={12} md={6}>
                  <b>Data: </b>
                  {estabelecimentoRow.fistData}
                </Grid>
                <Grid item xs={12} md={6}>
                  <b>Quantidade: </b>
                  {estabelecimentoRow.qtde}
                </Grid>
                <Grid item xs={12} md={6}>
                  <b>Faturamento Mdio: </b>
                  {estabelecimentoRow.faturamentoMedio}
                </Grid>
              </Grid>
            </div>
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button variant="contained" onClick={handleClose} color="primary">
            Fechar
          </Button>
        </DialogActions>
      </Dialog>
      <Dialog
        open={openEstablismentModal}
        onClose={handleCloseModelFail}
        aria-labelledby="alert-dialog-title"
        aria-describedby="alert-dialog-description"
        className={classes.dialogBox}
      >
        <DialogTitle id="alert-dialog-title">
          Informaes do Estabelecimento
        </DialogTitle>
        <DialogContent>
          <DialogContentText id="alert-dialog-description">
            <TextareaAutosize
              aria-label="minimum height"
              minRows={5}
              placeholder="Motivo.."
              style={{ width: "100%" }}
              id="motivo"
              onChange={handleChange}
              value={estabelecimentoPayload.motivo}
            />
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button variant="contained" onClick={handleFail} color="primary">
            Reprovar
          </Button>
          <Button
            variant="contained"
            onClick={handleCloseModelFail}
            color="primary"
          >
            Fechar
          </Button>
        </DialogActions>
      </Dialog>
    </div>
  );
  
  
}
export default SentFile;